import { observer } from 'mobx-react-lite'
import { useContext } from 'react'
import { RootStoreContext } from '../store/rootStoreContext'
import { Box, Typography } from '@mui/material'
import Menu from './menu'
import HelpPopup from './helpPopup'

const BottomBar = (props) => {
  const [root] = useContext(RootStoreContext)
  const { singleLabel = null } = props

  const bottomBarStyle = {
    position: 'absolute',
    bottom: '0px',
    height: '45px',
    width: '100%',
    borderTop: '1px solid gray',
    background: 'lightyellow',
  }

  const singleLabelStyle = {
    textAlign: 'center',
    width: '100%',
  }

  const doubleLabelContainerStyle = {}

  const leftLabelStyle = {
    textAlign: 'right',
    position: 'absolute',
    right: 'calc(50% + 15px)',
  }

  const centerPipeStyle = {
    position: 'absolute',
    left: '50%',
    transform: 'translateX(-50%)',
  }

  const rightLabelStyle = {
    textAlign: 'left',
    position: 'absolute',
    left: 'calc(50% + 15px)',
  }

  const helpPopupStyle = {
    position: 'absolute',
    right: '5px',
  }

  /*
  singleLabel
  leftLabel
  rightLabel
  leftAction
  rightAction
  */
  return (
    <Box id='bottomBar' sx={bottomBarStyle}>
      {singleLabel && <Box sx={singleLabelStyle}>{singleLabel}</Box>}
      {!singleLabel && (
        <Box>
          <Menu sx={{ position: 'absolute', left: '5px' }} />
          <Box sx={doubleLabelContainerStyle}>
            <Typography sx={leftLabelStyle}>daily</Typography>
            <Typography sx={centerPipeStyle}>|</Typography>
            <Typography sx={rightLabelStyle}>practice</Typography>
          </Box>
          <HelpPopup sx={helpPopupStyle} />
        </Box>
      )}
    </Box>
  )
}

export default observer(BottomBar)
